@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #000000;
    color: white;
    padding: 10px;
}

.footer-link {
    color: white;
}

.footer-img {
    width: calc(80vh/5 + 40px);
}
*{padding:0;margin:0;box-sizing:border-box}body{font-family:'Poppins', sans-serif}.container{max-width:70rem;padding:0 2rem;margin:0 auto}header{width:100%;height:160vh;position:relative;overflow:hidden}.mountain1{position:absolute;z-index:19;top:-250px}.person{position:absolute;z-index:20;top:-220px}.mountain2{position:absolute;z-index:18}.mountain3{position:absolute;top:-166px;z-index:17}.mountain4{position:absolute;z-index:16}.sky{position:absolute;bottom:0}.sky_two{position:absolute;width:570px;bottom:208px;right:-220px}.sky_three{position:absolute;width:570px;bottom:208px;left:-220px}nav{position:absolute;top:0;left:0;width:100%;padding:1rem 0;z-index:30;background:linear-gradient(to bottom, rgba(0,0,0,0.05), transparent)}nav .container{display:flex;justify-content:space-between;align-items:center}.logo{color:#000000;font-size:1.1rem;text-transform:uppercase;font-weight:600;letter-spacing:2px}.logo span{font-weight:300}.hamburger-menu{width:3rem;height:3rem;cursor:pointer;display:flex;align-items:center;justify-content:flex-end}.bar{position:relative;width:1.2rem;height:3px;background-color:#000000;border-radius:2px}.bar:before,.bar:after{content:'';position:absolute;width:1.55rem;height:3px;right:0;background-color:#000000;border-radius:2px}.bar:before{transform:translateY(-8px)}.bar:after{transform:translateY(8px)}.big-title{position:absolute;z-index:18;line-height:4rem;top:calc(25% - 4rem);width:300px;height:300px;text-align:center;font-size:5rem;font-weight:600;color:#fff}section{width:100%;position:relative}section .container{padding:3rem;align-items:center;justify-content:center}section img{width:100%}section .container>*{margin:1.5rem}.content{color:#000000}.title{color:#000000;font-weight:600;font-size:1.5rem;margin-bottom:1rem;padding-bottom:.5rem;position:relative}.color-title{color:white !important}.border{position:absolute;width:0%;height:3px;background-color:#000000;border:none !important;bottom:0;left:0}.shadow{position:absolute;bottom:100%;height:500px;width:100%;left:0;z-index:20;background:linear-gradient(to top, #fff, transparent)}.opacity{opacity:100}.custom_center{display:flex;justify-content:center;align-items:center;flex-direction:column}.evenly{justify-content:space-evenly}.card_holder{max-width:800px}.card_title{font-size:1.2rem}.color_section{background:#000000;color:white}iframe{object-fit:fill}@media (min-aspect-ratio: 16 / 9){iframe{width:177.78vh}}@media (max-aspect-ratio: 16 / 9){iframe{height:56.25vw}}@media (max-width: 850px){section .container{grid-template-columns:1fr}}@media (max-width: 600px){.big-title{font-size:3rem;z-index:20}.text{font-size:.8rem}.title{font-size:1.2rem}}@media (min-height: 1300px){header{height:100vh}}@media (min-width: 1900px){.responsive-width{width:100%}}@media (min-width: 1300px){.mountain2{width:100%}}.Typist .Cursor{display:inline-block}.Typist .Cursor--blinking{opacity:1;-webkit-animation:blink 1s linear infinite;animation:blink 1s linear infinite}@-webkit-keyframes blink{0%{opacity:1}50%{opacity:0}100%{opacity:1}}@keyframes blink{0%{opacity:1}50%{opacity:0}100%{opacity:1}}

